.region-wrapper{
    width:95%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin: 30px auto;
    height:100%;
    text-decoration: none;
    overflow-y: scroll; 
    overflow-x: hidden;
   

    .light-region{
        height:370px;
        max-width:300px;
        width:100%;
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        justify-content: flex-start;
        margin:20px 30px;
        background-color: #ffffff;
        padding:30px 20px;
        cursor: pointer;
        transition:  ease-in-out 500ms;
        img{
            width:100%;
            margin-bottom:5px;
            cursor: pointer;
        }

        .light{
            font-size:1rem;
            color:#5c5c5c;
            font-family:"bold";
            margin:5px 0;
            cursor: pointer;

           
        }

        .light-font{
            font-size:15px;
            margin-bottom:5px;
            font-family:"normal";
            color:#5c5c5c;
            .light-span{
                font-size:15px;
                font-family:"italic";
                color:#5c5c5c;
                margin-left:5px;
            }
        
        }
    }
    .light-region:hover{
        border:1px solid #f6f6f6;
        box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
        transition:  ease-in-out 1s;
        scale: (1.1);
    }
}


    .region-wrapper{
        width:95%;
        display: flex;
        align-items: center;
        justify-content:space-between;
        flex-wrap: wrap;
        height:100vh;
        margin:30px auto;
        text-decoration: none;
        overflow-y: scroll; 
        overflow-x: hidden;
        
    .dark-region{
        height:370px;
        max-width:300px;
        width:100%;
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        justify-content: flex-start;
        margin:20px 30px;
        background-color: #2b3743;
        padding:30px 20px;
        cursor: pointer;
        transition:  ease-in-out 500ms;

        img{
            width:100%;
            margin-bottom:5px;
            cursor: pointer;
        }

        .dark{
            font-size:1rem;
            color:#d9dfe5;
            font-family:"bold";
            margin:5px 0;
            cursor: pointer;
        }
        .dark-font{
            font-size:15px;
            margin-bottom:5px;
            font-family:"normal";
            color:#d9dfe5;

            .dark-span{
                font-size:15px;
                font-family:"italic";
                color:#808a93;
                margin-left:5px;
            }
          
         
        }
    }
    .dark-region:hover{
        border:1px solid #1d2935;
        box-shadow:0px 0px 8px #dddddd;
        transition:  ease-in-out 0.5s;
        scale: (1.1);
        border-radius:5px;
    }
}
    


    .region-wrapper::-webkit-scrollbar {
        width: 0; 
      }

      .region-wrapper::-webkit-scrollbar-track {
        background-color: #f1f1f1;
      }
      .region-wrapper::-webkit-scrollbar-thumb {
        background-color: #888;
      }


      @media(max-width:1137px){
        .region-wrapper{
            width:100%;
            justify-content: center;

        }
      }

     @media(max-width:450px){
        
        .region-wrapper{
           
            .light-region{
                margin:20px 0;
            }

            .dark-region{
                margin:20px 0;
            }
        }     
     }